import React from 'react'

import Layout from '../components/layout'
import SEO from '../components/seo'

export default ({ pageContext: { data } }) => {
  const { reply, description, responses } = data
  return (
    <Layout>
      <SEO title={`${reply} SMTP code`} />
      <section>
        <h3>Reply code {reply}</h3>
        <p>{description}</p>
      </section>

      {providers.map(provider => (
        <Provider key={response.id} provider={provider}>
          {provider.responses.map(item => (
            <Response key={item.status} data={item} />
          ))}
        </Provider>
      ))}
    </Layout>
  )
}

const Provider = ({ children, provider }) => {
  const { id, name } = provider

  return (
    <section>
      <h3 name={id}>{name}</h3>
      {children}
    </section>
  )
}

const Response = ({ data }) => {
  const { status, response } = data
  return (
    <div name={status}>
      <pre>{response}</pre>
    </div>
  )
}
